import { DirektoriComponent } from './direktori.component'
import { NgModule } from '@angular/core'
import { Routes, RouterModule } from '@angular/router'


const routes: Routes = [
    {
        path: '',
        redirectTo: 'employee'
    },
    {
        path: 'employee',
        loadChildren: () => import('./employee/employee.module').then(mo => mo.EmployeeModule)
    },
    {
        path: 'hr-management',
        loadChildren: () => import('./hrmanagement/hrmanagement.module').then(mo => mo.HrmanagementModule)
    },
    {
        path: 'general',
        loadChildren: () => import('./general/general.module').then(mo => mo.GeneralModule)
    },
    {
        path: 'development',
        loadChildren: () => import('./development/development.module').then(mo => mo.DevelopmentModule)
    },
    {
        path: 'pengaturan',
        loadChildren: () => import('./pengaturan/pengaturan.module').then(mo => mo.PengaturanModule)
    },


]

@NgModule({
    imports: [RouterModule.forChild(routes)],
    exports: [RouterModule]
})
export class DirektoriRoutingModule { }

