import { Component, OnInit } from '@angular/core'
import {
    slideFadeinUp,
    slideFadeinRight,
    zoomFadein,
    fadein,
} from 'src/app/layouts/App/router-animations'
import { Store, select } from '@ngrx/store'
import { RouterOutlet } from '@angular/router'
import { Observable } from 'rxjs'
import * as Reducers from 'src/app/store/reducers'
import { MenuService } from 'src/app/services/menu.service'

@Component({
    selector: 'app-direktori',
    templateUrl: './direktori.component.html',
    styleUrls: ['./direktori.component.scss'],
    animations: [slideFadeinUp, slideFadeinRight, zoomFadein, fadein],
})
export class DirektoriComponent implements OnInit {
    settings$: Observable<any>
    menuLayoutType: string
    isContentNoMaxWidth: boolean
    isAppMaxWidth: boolean
    isGrayBackground: boolean
    isSquaredBorders: boolean
    isCardShadow: boolean
    isBorderless: boolean
    isTopbarFixed: boolean
    isGrayTopbar: boolean
    routerAnimation: string

    constructor(private store: Store<any>, private menuService: MenuService) {
        this.store.pipe(select(Reducers.getSettings)).subscribe(state => {
            this.menuLayoutType = state.menuLayoutType
            this.isContentNoMaxWidth = state.isContentNoMaxWidth
            this.isAppMaxWidth = state.isAppMaxWidth
            this.isGrayBackground = state.isGrayBackground
            this.isSquaredBorders = state.isSquaredBorders
            this.isCardShadow = state.isCardShadow
            this.isBorderless = state.isBorderless
            this.isTopbarFixed = state.isTopbarFixed
            this.isGrayTopbar = state.isGrayTopbar
            this.routerAnimation = state.routerAnimation
        })
    }

    menuData: any = []
    ngOnInit() {
        this.menuData = [

            {
                title: 'All Staff',
                key: 'staf',
                icon: 'fe fe-bookmark',
                url: '/hr/direktori/pegawai',
            },
            {
                category: true,
                title: 'Status',
            },
            {
                title: 'contract',
                key: 'kontrak',
                icon: 'fe fe-bookmark',
                url: '/hr/direktori/module/kontrak',
            },
            {
                title: 'Non Active',
                key: 'mantan',
                icon: 'fe fe-bookmark',
                url: '/hr/direktori/module/mantan',
            },

            {
                category: true,
                title: 'Requirement',
            },
            {
                title: 'Applicants',
                key: 'pelamar',
                icon: 'fe fe-bookmark',
                url: '/hr/direktori/pelamar',
            },

            {
                title: 'Required Staff',
                key: 'permintaan-pegawai',
                icon: 'fe fe-bookmark',
                url: '/hr/direktori/permintaan-pegawai',
            },
            {
                category: true,
                title: 'Legal',
            },
            {
                title: 'training',
                key: 'training',
                icon: 'fe fe-bookmark',
                url: '/hr/direktori/training',
            },
            {
                title: 'license',
                key: 'lisensi',
                icon: 'fe fe-bookmark',
                url: '/hr/direktori/lisensi',
            },
            {
                title: 'project',
                key: 'proyek',
                icon: 'fe fe-bookmark',
                url: '/hr/direktori/proyek',
            },
            {
                title: 'kpi',
                key: 'kpi',
                icon: 'fe fe-bookmark',
                url: '/hr/direktori/module/kpi',
            },

            {
                title: 'setting',
                key: 'pengaturan',
                icon: 'fe fe-home',
                count: 6,
                children: [
                    {
                        title: 'Employee status',
                        key: 'status',
                        url: '/hr/direktori/pengaturan/status',
                    },
                    {
                        title: 'warning',
                        key: 'sp',
                        url: '/hr/direktori/pengaturan/sp',
                    },
                    {
                        title: 'license',
                        key: 'jlisensi',
                        url: '/hr/direktori/pengaturan/jlisensi',
                    }
                ],
            },
        ]
        this.menuService.menu.next(this.menuData)

        this.menuService.subBar_info.next({ parent: 'HR', child: 'Direktori' })
    }


    routeAnimation(outlet: RouterOutlet, animation: string) {
        if (animation === this.routerAnimation) {
            return outlet.isActivated && outlet.activatedRoute.routeConfig.path
        }
    }
}
